import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { LoaderLayerComponent } from './components/loaderlayer/loaderlayer.component';

import { DefaultValuePipe } from './pipes/defaultvalue.pipe';
import { SafeHtmlPipe }  from './pipes/safehtml.pipe';
import { AssetsPipe }  from './pipes/assets.pipe';
import { LocalDatePipe } from './pipes/localdate.pipe'

@NgModule({
    imports: [CommonModule],
    exports: [LoaderLayerComponent, SafeHtmlPipe, DefaultValuePipe, AssetsPipe, LocalDatePipe],
    declarations: [LoaderLayerComponent, SafeHtmlPipe, DefaultValuePipe, AssetsPipe, LocalDatePipe],
    providers: [],
})
export class AppCommonModule { }



