import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Subject } from 'rxjs/Subject';

export interface MQMSG {
    from?: string,
    type: string,
    load: any
}

interface MQSLOT {
    [key: string]: Subject<MQMSG>
}


@Injectable()
export class MQService {
    protected static queues: MQSLOT;
    protected static noListenerMessages: { [key: string]: any[] } = {};

    constructor() {
        console.log('MQService constructor')
        MQService.queues = {};
    }

    sendMessage(slot: string, data: MQMSG) {
        if (MQService.queues[slot] === undefined) {
            this.pushtoNoListenerQueue(slot, data);
        }
        this.getSlot(slot).next(data);
    }

    private pushtoNoListenerQueue(slot: string, data: MQMSG) {
        if (MQService.noListenerMessages[slot] === undefined) {
            MQService.noListenerMessages[slot] = [];
        }
        MQService.noListenerMessages[slot].push(data);
    }

    protected checkNoListenerQueue(slot) {
        if (MQService.noListenerMessages[slot] !== undefined) {
            Observable.timer(5).subscribe(x => {
                for (const data of MQService.noListenerMessages[slot]) {
                    console.log('attach messages which created before listener.');
                    this.getSlot(slot).next(data);
                }
                delete MQService.noListenerMessages[slot];
            });
        }
    }

    getMessage(slot: string): Observable<MQMSG> {
        this.checkNoListenerQueue(slot);
        return this.getSlot(slot).asObservable();
    }

    clearMessage() {
        for (const slot in MQService.queues) {
            if (MQService.queues.hasOwnProperty(slot)) {
                MQService.queues[slot].next();
            }
        }

        for (const slot in MQService.noListenerMessages) {
            if (MQService.noListenerMessages.hasOwnProperty(slot)) {
                delete MQService.noListenerMessages[slot];
            }
        }

        MQService.noListenerMessages = {};
    }

    clearSlot(slot: string): void {
        if (MQService.queues[slot] !== undefined) {
            delete MQService.queues[slot];
        }
    }

    private getSlot(slot: string): Subject<MQMSG> {
        if (MQService.queues[slot] === undefined) {
            MQService.queues[slot] = new Subject<MQMSG>();
        }
        return MQService.queues[slot];
    }
}


