import { Component, OnInit, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { MQService,  GlobalVarService } from '@ngxsuit';

@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss'],
    providers:[]
})
export class AppComponent {
    constructor(private translate: TranslateService, private mq:MQService, private elementRef:ElementRef, private gv:GlobalVarService) {
        // this.fetchGlobalVars(elementRef);
        this.gv.initGlobalVarsFromRootElement(elementRef);
        localStorage.setItem("authToken", this.gv.get('token'));
        this.initTranslate(translate);
    }

    private initTranslate(translate: TranslateService) {
        translate.addLangs(['en', 'fr', 'ur']);
        translate.setDefaultLang('en');

        const browserLang = translate.getBrowserLang();
        translate.use(browserLang.match(/en|fr|ur/) ? browserLang : 'en');
    }
}


