import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { MQService } from '../../services/mq.service';
import { GlobalVarService } from '../../services/global.var.service';
import { SafeHtmlPipe } from '../../pipes/safehtml.pipe';

interface CONTROL_MSG {
    loading: boolean,
    error?: string,
    tip?: string,
    html?: string,
    delay?: number
}

@Component({
    selector: 'app-loader',
    templateUrl: './loaderlayer.component.html',
    styleUrls: ['./loaderlayer.component.scss']
})

export class LoaderLayerComponent implements OnInit, OnDestroy {
    @Input() loading: boolean;
    controlMsg: CONTROL_MSG;
    subscription: Subscription;
    private image = '';

    constructor(private mq: MQService, private gv: GlobalVarService) { }

    ngOnInit() {
        console.log('loader layer init')
        this.image = this.assets('/images/loader.gif')
        let originStatus = this.loading;
        if (!originStatus) {
            this.loading = true;
        }
        this.controlMsg = { loading: this.loading, tip: '', delay: 0, error: '', html: '' };
        this.subscription = this.mq.getMessage('app-loader').subscribe(message => {
            let msg: CONTROL_MSG = <CONTROL_MSG>message.load;
            this.controlMsg.tip = (msg.tip === undefined ? '' : msg.tip);
            this.controlMsg.error = (msg.error === undefined ? '' : msg.error);
            this.controlMsg.delay = (msg.delay === undefined ? 0 : msg.delay);
            this.controlMsg.html = (msg.html === undefined ? '' : msg.html);

            if (msg.loading) {
                this.loading = this.controlMsg.loading;
            } else {
                if (msg.delay !== undefined) {
                    setTimeout(() => {
                        this.loading = false;
                        this.controlMsg.tip = '';
                        this.controlMsg.error = '';
                        this.controlMsg.html = '';
                    },
                        msg.delay * 1000
                    );
                } else {
                    this.loading = false;
                    this.controlMsg.tip = '';
                    this.controlMsg.error = '';
                    this.controlMsg.html = '';
                }
            }
        });

        if (!originStatus) {
            this.loading = false;
        }
    }

    ngOnDestroy() {
        this.subscription.unsubscribe();
    }

    private assets(suffix: string = ''): string {
        const prefix = this.gv.getString('assets');
        return `${prefix}${suffix}`;
    }
}



