import { Component, OnInit, OnDestroy } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';

// import { MLevelItemsParserProvider, MenuProvider } from '@ngxsuit';
import { MLevelItemsParser, MLevelsBaseItemInterface, MENUS_DEFINES} from '@ngxsuit';
import { MENUITEM, SYSTEM_CONFIGURATION_ITEM } from '../../../interfaces'
import { UISchemaService } from 'Admin/services/ui.schema.service';

import { MQService } from '@ngxsuit'

@Component({
    selector: 'app-sidebar',
    templateUrl: './sidebar.component.html',
    styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent implements OnInit, OnDestroy {
    isActive = false;
    showMenu = '';
    menus:SYSTEM_CONFIGURATION_ITEM[] = [];
    selected:string = '';
    private mqsub: Subscription
    
    constructor(private uischema: UISchemaService, private mq:MQService ) {
    }

    ngOnInit() {
        this.mqsub = this.mq.getMessage('SidebarComponent').subscribe(msg => {
            if (msg.type == 'ui-schema-fetched') {
                this.menus = msg.load;
            }
        });
        this.uischema.wait4UISchema('SidebarComponent');
    }

    ngOnDestroy() {
        this.mqsub.unsubscribe();
    }

    onClickRouteLink(menu) {
        this.selected = menu.name; 
        if (menu.absolute) { 
            window.location.href = menu.link; 
        }
    }
}



