import { HttpClient } from '@angular/common/http';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule, Http } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
// import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { AuthGuard } from './shared';
import { HTTPCLIENT_AUTH_PLUGIN, MQService, GlobalVarService, WindowRef, LocaltimeService } from '@ngxsuit';

import { UISchemaService } from './services/ui.schema.service'

// import { LoaderLayerComponent, DefaultValuePipe, SafeHtmlPipe, AssetsPipe } from '@ngxsuit';

class TranslateHttpLoader{
    constructor(private http: HttpClient,  private gv:GlobalVarService, private prefix: string = "/i18n/", private suffix: string = ".json") {}
    public getTranslation(lang: string): any {
        let url_prefix = this.gv.getString('assets')
        return this.http.get(`${url_prefix}${this.prefix}${lang}${this.suffix}`);
    }
}

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient,  gv:GlobalVarService) {
    return new TranslateHttpLoader(http, gv);
}

@NgModule({
    declarations: [
        AppComponent
    ],
    imports: [
        BrowserModule,
        FormsModule,
        HttpClientModule,
        HttpModule,
        AppRoutingModule,
        TranslateModule.forRoot({
            loader: {
                provide: TranslateLoader,
                useFactory: HttpLoaderFactory,
                deps: [HttpClient, GlobalVarService]
            }
        })
    ],
    providers: [
        AuthGuard, HTTPCLIENT_AUTH_PLUGIN, MQService, GlobalVarService, WindowRef, LocaltimeService, UISchemaService
    ],
    bootstrap: [AppComponent]
})
export class AppModule { }



