import { Injectable } from '@angular/core';
import { ElementRef } from '@angular/core';

interface GLOBAL_VARS {
    [key:string]:string;
}

@Injectable()
export class GlobalVarService {
    protected static vars:GLOBAL_VARS = {};

    constructor() {
        console.log('GlobalVarService constructor')
    }

    get(key:string) {
        return GlobalVarService.vars[key];
    }

    getString(key:string):string {
        if (GlobalVarService.vars[key]) {
            return <string>GlobalVarService.vars[key];
        }
        return '';
    }
    
    set(key:string, value:any) {
        GlobalVarService.vars[key] = value;
    }

    initGlobalVarsFromRootElement(rootElement:ElementRef) {
        let attr:string = rootElement.nativeElement.getAttribute('globals');
        let items =  attr.split(';');
        let globals = {};
        for(let item of items) {
            let values = item.split('=');
            this.set(values[0], values[1]);
        }
    }
}


