// How to use
// put GLOBAL_ROUTEDEFS to your layout.module.ts file
//  
// @NgModule({
//     imports: GLOBAL_ROUTEDEFS,
//     exports: [RouterModule]
// })

import { Routes, RouterModule } from '@angular/router';

import { ROOTROUTES as AdminROOTROUTES } from "Admin/assembly";
import { CHILDROUTES as AdminCHILDROUTES } from "Admin/assembly";
import { MENUS as AdminMENUS } from "Admin/assembly";

export { AppModule } from 'Admin/app.module';

const NOTFOUNDROUTES: Routes = [
{ path: 'not-found', loadChildren: 'Admin/not-found/not-found.module#NotFoundModule' },
{ path: '**', redirectTo: 'not-found' }
];

export const ROOTROUTES_DEFINES = [ RouterModule.forRoot(AdminROOTROUTES),RouterModule.forRoot(NOTFOUNDROUTES) ];

export const CHILDROUTES_DEFINES = [ RouterModule.forChild(AdminCHILDROUTES) ];

export const MENUS_DEFINES = [ AdminMENUS ];


