import { Component, OnInit, OnDestroy, ViewChild, ViewContainerRef, ElementRef } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { MQService, GlobalVarService } from '@ngxsuit';
import { Subscription } from 'rxjs/Subscription';
import { HttpClient } from '@angular/common/http';

interface MQ_MSG {
    type:string,
    load:any
}

interface NOTIFY_MSG {
    brief:string,
    detail:string
    time?:number,
}


@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnDestroy {
    title:string = '';
    subscription:Subscription;
    @ViewChild('layoutheaderbar', { read: ViewContainerRef }) _headerbar;
    notifies:[NOTIFY_MSG];
    admin:any = {};

    constructor(private translate: TranslateService, public router: Router, private mq:MQService, private gv:GlobalVarService, private http:HttpClient) {
        this.router.events.subscribe((val) => {
            if (val instanceof NavigationEnd && window.innerWidth <= 992) {
                this.toggleSidebar();
            }
        });
    }

    toggleSidebar() {
        const dom: any = document.querySelector('body');
        dom.classList.toggle('push-right');
    }

    rltAndLtr() {
        const dom: any = document.querySelector('body');
        dom.classList.toggle('rtl');
    }

    onLoggedout() {
        console.log('onLoggedout');
        this.http.post(this.gv.getString('admin_logout'), {
            _token: this.gv.getString('csrf_token'),
            is_ajax:1
        }, { withCredentials: true }).subscribe(data => {
            localStorage.removeItem('admin_login');
            this.router.navigate(['/login']);
        });
    }

    changeLang(language: string) {
        this.translate.use(language);
    }

    ngOnInit() {
        this.admin = JSON.parse(localStorage.getItem('admin_login'));

        if (localStorage.getItem('PRIVATE_NOTIFIES')) {
            this.notifies = JSON.parse(localStorage.getItem('PRIVATE_NOTIFIES'));
        } else {
            this.notifies = [{
                brief:'No Message',
                time:Date.now(),
                detail:''
            }];
        }

        this.subscription = this.mq.getMessage('headercompoment').subscribe(message => { 
            let msg:MQ_MSG = <MQ_MSG>message;
            switch(msg.type) {
                case 'title':
                    this.title = msg.load;
                    break;
                case 'headerbar':
                    this._headerbar.remove(0);
                    if (msg.load) {
                        this._headerbar.createEmbeddedView(msg.load);
                    }
                    break;
                case 'notify':
                    this.handlePrivteNotify(<NOTIFY_MSG>msg.load);
                    break;
            }
        });

        
    }

    private handlePrivteNotify(notify:NOTIFY_MSG) {
        notify.time = Date.now();
        this.notifies.unshift(notify);
        if (this.notifies) {
            while(this.notifies.length>20) {
               this.notifies.pop();
            }
            localStorage.setItem('PRIVATE_NOTIFIES', JSON.stringify(this.notifies));
        }
    }

    ngOnDestroy() {
        this.subscription.unsubscribe();
    }
}



