// import { Observable } from 'rxjs/Observable';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/catch';

import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {HttpEvent, HttpResponse, HttpInterceptor, HttpHandler, HttpRequest, HTTP_INTERCEPTORS} from '@angular/common/http';
import { MQService } from './mq.service'
import { GlobalVarService } from './global.var.service'

@Injectable()
export class HttpWithAuth implements HttpInterceptor {
  constructor(private mq: MQService, private gv: GlobalVarService, private router: Router) {}
  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    console.log('OVERWRITE REQUEST', req)
    const authReq = req.clone({headers: req.headers.set('Authorization', this.retrieveAuthToken())});
    // return next.handle(authReq);
    return next.handle(authReq)
      .catch(err => {
        if (err.status === 401) {
          this.router.navigate(['/login']);
          return;
        }
        console.log('Caught error', err.error);
        this.mq.sendMessage('app-loader', {type: '', load: {loading: false, html: err.error} });
        return Observable.throw(err);
      });
  }

  private retrieveAuthToken(): string {
    const current_token = this.gv.getString('curent_auth_token');
    if (current_token !== '') {
      console.log('use token:', current_token);
      this.gv.set('curent_auth_token', false);
      return current_token;
    }

    const value = localStorage.getItem('authToken');
    return !value ? '' : value;
  }
}

export const HTTPCLIENT_AUTH_PLUGIN = {
    provide: HTTP_INTERCEPTORS,
    useClass: HttpWithAuth,
    multi: true,
}



